* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
}

body {
  background-color: #150d17;
}

img {
  max-width: 100%;
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-active {
  opacity: 0;
}

.refract {
  position: absolute;
}

.refract .leading1,
.refract .leading2,
.refract .block,
.refract .trailing1,
.refract .trailing2 {
  width: 100%;
  height: 100%;
  position: absolute;
}

.refract .leading1 {
  background-color: #1027ee;
  box-shadow: 2px 0 1px #1027ee;
  z-index: 2;
}

.refract .leading2 {
  background-color: #2aecef;
  box-shadow: 1px 0 2px #2aecef;
  z-index: 3;
}

.refract .block {
  background-color: #FFFFFF;
  box-shadow: 0 0 1px #FFFFFF;
  z-index: 4;
}

.refract .trailing1 {
  background-color: #f2f235;
  box-shadow: -1px 0 1px #f2f235;
  z-index: 3;
}

.refract .trailing2 {
  background-color: #ef1c1a;
  box-shadow: -2px 0 1px #ef1c1a;
  z-index: 2;
}
